@media screen and (orientation: portrait) {
	:root {
		--layout-padding-v: 2vw;
		--layout-padding-h: 5vw;
	}
}

@media screen and (orientation: landscape) {
	:root {
		--layout-padding-v: 5vh;
		--layout-padding-h: 20vh;
	}
}

 
#main-container {
	padding: 2%;
}


blockquote {
	border-left: 5px solid #eee;
	padding: 10px 20px;
	margin: 0 0 20px;
	page-break-inside: avoid;
}



